import React, { Fragment } from 'react'
import {
    Form,
    Input,
    InputNumber,
} from 'antd';
import { useFormik } from 'formik';
import { useDispatch } from 'react-redux'
import { GROUPID } from '../../../utils/setting';
import { Select } from 'antd'
import { addUserAction } from '../../../redux/actions/QuanLyNguoiDungAction';
import * as Yup from "yup"
const { Option } = Select;


export default function AddUser() {
    const phoneRegex = /([+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})\b/;
    const nameRegex = /^[a-zA-Z\s\W|_]+$/;
    const dispatch = useDispatch();

    const formik = useFormik({
        initialValues: {
            maLoaiNguoiDung: "",
            hoTen: "",
            taiKhoan: "",
            soDt: "",
            matKhau: "",
            manhom: GROUPID,
            email: "",
        },
        validationSchema: Yup.object({
            taiKhoan: Yup.string().required("Ti khon khng c b trng").min(6, "Ti khon t 6-32 k t").max(32, "Ti khon t 6-32 k t"),
            matKhau: Yup.string().required("Mt Khu khng c b trng"),
            hoTen: Yup.string().required("H Tn khng c b trng").matches(nameRegex, "H Tn khng hp l"),
            email: Yup.string().required("Email khng c b trng").email("Email khng ng nh dng"),
            soDt: Yup.string().required("S in thoi khng c b trng").matches(phoneRegex, "S in thoi khng hp l").min(10, "S in thoi ti thiu 10 s").max(10, "S in thoi ti thiu 10 s"),

        }),
        onSubmit: (values) => {
            dispatch(addUserAction(values))
        }
    })

    const handleSelectChange = (values) => {
        formik.setFieldValue("maLoaiNguoiDung", values);
    }

    return (
        <Fragment>
            <h3 className="c-admin-title">thm ngi dng</h3>
            <Form
                onSubmitCapture={
                    formik.handleSubmit
                }
                labelCol={{
                    span: 4,
                }}
                wrapperCol={{
                    span: 14,
                }}
                layout="horizontal"
                className="form-addfilm"
            >
                <Form.Item className="c-form__group" label="Ti khon">
                    <Input name="taiKhoan" onChange={formik.handleChange} />
                    {formik.touched.taiKhoan && formik.errors.taiKhoan ? (
                        <p className="error">{formik.errors.taiKhoan}</p>
                    ) : null}
                </Form.Item>
                <Form.Item className="c-form__group" label="Mt Khu">
                    <Input.Password name="matKhau" onChange={formik.handleChange} />
                    {formik.touched.matKhau && formik.errors.matKhau ? (
                        <p className="error">{formik.errors.matKhau}</p>
                    ) : null}
                </Form.Item>
                <Form.Item className="c-form__group" label="H Tn">
                    <Input name="hoTen" onChange={formik.handleChange} />
                    {formik.touched.hoTen && formik.errors.hoTen ? (
                        <p className="error">{formik.errors.hoTen}</p>
                    ) : null}
                </Form.Item>
                <Form.Item className="c-form__group" label="Email">
                    <Input name="email" onChange={formik.handleChange} />
                    {formik.touched.email && formik.errors.email ? (
                        <p className="error">{formik.errors.email}</p>
                    ) : null}
                </Form.Item>
                <Form.Item className="c-form__group" label="S in thoi">
                    <Input name="soDt" rows="4" onChange={formik.handleChange} />
                    {formik.touched.soDt && formik.errors.soDt ? (
                        <p className="error">{formik.errors.soDt}</p>
                    ) : null}
                </Form.Item>
                <Form.Item className="c-form__group" label="M Nhm">
                    <InputNumber disabled name="maNhom" value={GROUPID} />
                </Form.Item>
                <Form.Item className="c-form__group" label="Loi Ngi dng">
                    <Select
                        name="maLoaiNguoiDung"
                        placeholder="Chn loi ngi dng ..."
                        onChange={handleSelectChange}
                        allowClear
                        defaultValue="KhachHang"
                    >
                        <Option value="KhachHang">Khch Hng</Option>
                        <Option value="QuanTri">Qun Tr</Option>
                    </Select>
                    {formik.touched.maLoaiNguoiDung && formik.errors.maLoaiNguoiDung ? (
                        <p className="error">{formik.errors.maLoaiNguoiDung}</p>
                    ) : null}
                </Form.Item>
                <Form.Item className="c-form__group" label="Chc nng">
                    <button type="submit" className="c-main-btn c-main-btn--paddingsmall" >Thm Ngi Dng</button>
                </Form.Item>
            </Form>
        </Fragment>
    )
}
